<template>
    <div>
        <label class="switch">
            <input type="checkbox" @change="setMode" v-model="checked" />
            <span class="slider round"></span>
        </label>
    </div>
</template>
<script>
export default {
    data() {
        return {
            checked: false,
        };
    },
    methods: {
        setMode(event) {
            //console.log(event.target.checked);
            localStorage.setItem("darkMode", this.checked);
            document.getElementById("themeDiv").classList.toggle("dark");
        },
    },
    mounted() {
        this.checked = localStorage.getItem("darkMode");
        /*
        if (this.checked) {
            document.getElementById("themeDiv").classList.add("dark");
        }
        */
    },
};
</script>
<style scoped>
/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

input:checked + .slider {
    background-color: #6875f5;
}

input:focus + .slider {
    box-shadow: 0 0 1px #6875f5;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}
</style>
